import { request } from 'utils/index';

const prefix = '/api/students';


export const getStudents = ({
  classId,
  classname,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
  prefix,
  url: '/Student',
  method: 'GET',
  params: {
    classId,
    classname,
    page,
    pageSize,
    orderBy,
    isPagingEnabled
  },
  data: {}
});


export const searchStudents = ({
  studyFieldId,
  fullname,
  studentCode,
  trainTypeId,
  facultyId,
  courseId,
  classId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Student/Search',
    method: 'GET',
    params: {
      studyFieldId,
      fullname,
      studentCode,
      trainTypeId,
      facultyId,
      courseId,
      classId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const getStudentById = ({ id } = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });


export const deleteStudent = id =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'DELETE',
    params: {},
    data: {}
  });

export const updateStudent = ({
  id,
  candidateId,
  code,
  firstName,
  lastName,
  idCard,
  placeIssue,
  dateIssue,
  pobId,
  dob,
  gender,
  homeVillage,
  nationId,
  ethnicityId,
  religion,
  comeFromId,
  unionDate,
  politicalDate,
  permanentAddress,
  village,
  townId,
  districtId,
  policyObjectId,
  subsidizeObjectId,
  prioritizeObjectId,
  homePhone,
  phone,
  email,
  mailAddress,
  emergencyAddress,
  estimatedNote,
  foreignLanguage,
  liveAddress,
} = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'PUT',
    params: {},
    data: {
      candidateId,
      code,
      firstName,
      lastName,
      idCard,
      placeIssue,
      dateIssue,
      pobId,
      dob,
      gender,
      homeVillage,
      nationId,
      ethnicityId,
      religion,
      comeFromId,
      unionDate,
      politicalDate,
      permanentAddress,
      village,
      townId,
      districtId,
      policyObjectId,
      subsidizeObjectId,
      prioritizeObjectId,
      homePhone,
      phone,
      email,
      mailAddress,
      emergencyAddress,
      estimatedNote,
      foreignLanguage,
      liveAddress,
    },
  });

export const updateStudentUploadAvatar = ({ id, data } = {}) =>
  request({
    prefix,
    url: `/Student/${id}/UploadAvatar`,
    method: 'PUT',
    params: {},
    data,
  });



