import { request } from 'utils';

const prefix = '/api/categories';

export const getSubsidizeObjects = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/SubsidizeObject',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getSubsidizeObjectById = id =>
  request({
    prefix,
    url: `/SubsidizeObject/GetById`,
    method: 'GET',
    params: { id },
  });

export const deleteSubsidizeObject = id =>
  request({
    prefix,
    url: `/SubsidizeObject`,
    method: 'DELETE',
    data: { id },
  });

export const updateSubsidizeObject = ({ id, label, supportAmt }) =>
  request({
    prefix,
    url: `/SubsidizeObject`,
    data: { id, label, supportAmt },
    method: 'PUT',
  });

export const createSubsidizeObject = ({ id, label, supportAmt }) =>
  request({
    prefix,
    url: `/SubsidizeObject`,
    data: { id, label, supportAmt },
    method: 'POST',
  });



