import { request } from 'utils';

const prefix = '/api/categories';

export const getPolicyObjects = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/PolicyObject',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getPolicyObjectById = id =>
  request({
    prefix,
    url: `/PolicyObject/${id}`,
    method: 'GET',
  });

export const deletePolicyObject = id =>
  request({
    prefix,
    url: `/PolicyObject/${id}`,
    method: 'DELETE',
  });

export const updatePolicyObject = ({ id, label, decreasePct }) =>
  request({
    prefix,
    url: `/PolicyObject/${id}`,
    data: { id, label, decreasePct },
    method: 'PUT',
  });

export const createPolicyObject = ({ label, decreasePct }) =>
  request({
    prefix,
    url: `/PolicyObject`,
    data: { label, decreasePct },
    method: 'POST',
  });



