import { request } from 'utils';

const prefix = '/api/categories';

export const getPrioritizeObjects = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/PrioritizeObject',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getPrioritizeObjectById = id =>
  request({
    prefix,
    url: `/PrioritizeObject/${id}`,
    method: 'GET',
  });

export const deletePrioritizeObject = id =>
  request({
    prefix,
    url: `/PrioritizeObject/${id}`,
    method: 'DELETE',
  });

export const updatePrioritizeObject = ({ id, label }) =>
  request({
    prefix,
    url: `/PrioritizeObject/${id}`,
    data: { id, label },
    method: 'PUT',
  });

export const createPrioritizeObject = ({ label }) =>
  request({
    prefix,
    url: `/PrioritizeObject`,
    data: { label },
    method: 'POST',
  });



