import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { SelectAjax } from 'components/common/index';
import { getListTown } from 'services/town';
import { getListNation } from '../../../../services/nation';
import { getDistricts } from '../../../../services/district';
import { getComeFrom } from '../../../../services/comeFrom';

class FormInfoAddrBasic extends Component {
  state = {};

  render() {
    if (!this.props.form) return null;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical">
        <Col span={12}>
          <Form.Item label="Qu qun">{getFieldDecorator('homeVillage', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Quc tch">
            {getFieldDecorator('nationId', {})(<SelectAjax service={getListNation} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="TP xut thn">
            {getFieldDecorator('comeFromId', {})(<SelectAjax service={getComeFrom} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="C thng tr">
            {getFieldDecorator('permanentAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="X/Phng">{getFieldDecorator('village', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Tnh/Tp">
            {getFieldDecorator('townId', {})(<SelectAjax service={getListTown} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Qun/Huyn">
            {getFieldDecorator('districtId', {})(<SelectAjax service={getDistricts} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="a ch bo tin">
            {getFieldDecorator('emergencyAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Ni  hin nay">
            {getFieldDecorator('liveAddress', {})(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



