import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { SelectGender, InputGroupDate } from 'components/common/index';

class FormInfoBasic extends Component {
  state = {};
  render() {
    if (!this.props.form) return null;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical">
        <Col span={24}>
          <Form.Item label="Ngy sinh">
            {getFieldDecorator('dob', {})(<InputGroupDate style={{ width: '100%' }} />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Gii tnh">
            {getFieldDecorator('gender', {})(<SelectGender />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="T c nhn">{getFieldDecorator('phone', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Email">{getFieldDecorator('email', {})(<Input />)}</Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoBasic;



