import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { SelectAjax } from 'components/common/index';
import { getAreas } from 'services/area';

class FormInfoTS extends Component {
  state = {};

  render() {
    if (!this.props.form) return null;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical">
        <Col span={24}>
          <Form.Item label="Ngoi ng">
            {getFieldDecorator('foreignLanguage', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Khu vc">
            {getFieldDecorator('candidateData.areaId', {})(
              <SelectAjax service={getAreas} disabled />
            )}
          </Form.Item>
        </Col>

        <Col span={24}>
          <Form.Item label="S bo danh">
            {getFieldDecorator('candidateData.code', {})(<Input disabled />)}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="im tng cng">
            {getFieldDecorator('candidateData.totalGrade', {})(<Input disabled />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoTS;



